<template>
    <div>
        <div v-show="store.state.is_collapsed" class="overlay" @click="toggleSideMenu"><span class="overlay-text">STICK
                TO YOUR HABITS</span></div>
        <!-- 使用transition包裹SideMenu，添加el-aside的定位和z-index -->
        <transition name="slide">

            <SideMenu v-if="store.state.is_collapsed" class="side-menu"
                :class="{ 'is-visible': store.state.is_collapsed, 'menu-menuIn': store.state.is_collapsed }" />

        </transition>
        <el-header>
            <!-- 按钮保持不变 -->
            <button @click="toggleSideMenu">
                导航
            </button>
        </el-header>
        <div direction="vertical">
            <!-- 确保TopHeader不会导致布局问题，如果不需要可以注释掉或删除 -->
            <!-- <TopHeader/> -->
            <el-main>
                <router-view></router-view>
            </el-main>
        </div>
    </div>
</template>

<script setup>
import TopHeader from '../components/mainbox/TopHeader.vue'
import SideMenu from '../components/mainbox/SideMenu.vue'
import { reactive } from 'vue'
import { ref } from 'vue'
import store from '@/store/index'

const toggleSideMenu = () => {
    if (store.state.is_collapsed)
        store.commit("changeCollapsed", false)
    else
        store.commit("changeCollapsed", true)
}

</script>

<style>
@keyframes menuIn {

    0% {
        transform: translateY(-100%);
        width: 0%;
        /* 初始宽度 */
    }

    50% {
        transform: translateY(0);
        width: 1%;
        /* 初始宽度 */
    }

    100% {
        transform: skew(-21deg);
        width: 15%;
        /* 初始宽度 */
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(1, 36, 78);
    /* 灰色背景，50% 透明度 */
    z-index: 999;
    /* 确保遮罩层位于其他元素上方 */
    opacity: 0.8;
}

.overlay-text {
    color: #fff;
    /* 文字颜色 */
    position: absolute;
    /* 绝对定位 */
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* 居中显示 */
    font-size: 100px;
    /* 字体大小 */
    font-weight: bold;
    /* 字体加粗 */
    z-index: 1001;
    /* 确保文本在遮罩层之上 */
    transform: skew(-21deg);
}

.menu-menuIn {
    animation: menuIn 0.5s cubic-bezier(.36, .07, .19, .97);
    transform-origin: 50% 50%;
}

.slide-enter-active,
.slide-leave-active {
    transition: opacity 0.5s;
}

.slide-enter,
.slide-leave-to {
    opacity: 0;
}
</style>