<template>
    <div style="margin-top:30px;padding-left:10px;padding-right:10px">
        <div style="margin-top:30px;padding-left:10px;padding-right:10px">
            <el-row :gutter="20">
                <input style="margin-left:20px" type="textarea" placeholder="请输入内容" v-model="questionForm.my_question"
                    maxlength="500" show-word-limit :autosize="{ minRows: 2 }" resize="none" />

                <el-col :span="8">
                    <button class="question_button" @click="sendQuestion" type="primary" :loading="loading"><span>{{
                loading ?
                    'waiting...' : 'send' }}</span></button>
                </el-col>
            </el-row>
            <div v-for="(chat, index) in chatContents" :key="index">
                <p style="color:green">我：{{ chat.user }}</p>
                <div style="color:red">habitAI：
                    <div v-html="chat.ui"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { reactive, ref } from 'vue';
import axios from 'axios';
import { ElMessage } from 'element-plus';
import MarkdownIt from 'markdown-it';
import { List, Opportunity, Avatar } from '@element-plus/icons-vue'

const md = new MarkdownIt();
var loading = ref(false);

const questionForm = reactive({
    my_question: "",
});

let s = ''
const chatContents = ref([]);
const messageQueue = []; // 用于存储消息队列
let isDisplaying = false; // 是否正在显示消息
const sendQuestion = () => {
    let question = questionForm.my_question.trim();
    questionForm.my_question = '';
    if (!question) {
        return;
    }
    loading.value = true;
    axios.post("/adminapi/user/sendquestion", {
        question: question
    }, {
        headers: {
            'Authorization': `Bearer ${localStorage.getItem("token")}`
        },
    }).then(res => {
        let question_id = res.data.questionId;
        if (question_id) {
            let answer = ref('');
            let ui = ref('');
            chatContents.value.push({ user: question, ai: answer, ui: ui });
            const eventSource = new EventSource(`/adminapi/user/getanswer/${question_id}`);

            eventSource.onmessage = (event) => {
                const fullMessage = decodeURIComponent(event.data);
                console.log(fullMessage);
                messageQueue.push(fullMessage);
                if (!isDisplaying) {
                    displayMessages();
                }
            };

            eventSource.onerror = () => {
                console.log(s);
                eventSource.close();
                loading.value = false;
            };
        }
    }).catch(error => {
        console.error("Error sending question: ", error);
        ElMessage.error("Something error...");
        loading.value = false;
    })
};
const displayMessages = () => {
    if (messageQueue.length === 0) {
        isDisplaying = false;
        return;
    }
    isDisplaying = true;
    const fullMessage = messageQueue.shift();
    let currentIndex = 0;
    const baseSpeed = 30; // Base speed in ms per character
    const minSpeed = 10;  // Minimum speed in ms per character
    const maxSpeed = 70; // Maximum speed in ms per character

    const answerRef = chatContents.value[chatContents.value.length - 1];
    s += fullMessage;
    const typeWriter = () => {
        if (currentIndex < fullMessage.length) {
            answerRef.ai += fullMessage.charAt(currentIndex);
            answerRef.ui = md.render(answerRef.ai);
            currentIndex++;
            setTimeout(typeWriter, Math.max(minSpeed, Math.min(maxSpeed, baseSpeed - fullMessage.length * 2)));
        } else {
            displayMessages(); // 继续显示下一条消息
        }
    };

    typeWriter();
};
</script>

<style lang="scss" scoped>
.question_button {
    width: 150px;
    height: 100%;
    border: none;
    transform: rotate(5deg);
    transform-origin: center;
    font-family: "Gochi Hand";
    font-size: 20px;
    box-shadow: 2px 4px 0 #353535;
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.question_button span {
    background: #ffffff;
    display: block;
    padding: 0.5rem 1rem;
    border: 1px solid #000000;
}

.question_button:active {
    transform: translateY(5px);
    padding-bottom: 0px;
    outline: 0;
}
</style>
